import React from "react";

const Text = ({ text, playMusic, playing }) => {
  return (
    <div className="main">
      <div className="music">
        <div className={playing ? "muzieknootjes" : ""}>
          <div className="noot-1">&#9835; &#9833;</div>
          <div className="noot-2">&#9833;</div>
          <div className="noot-3">&#9839; &#9834;</div>
          <div className="noot-4">&#9834;</div>
        </div>
        <div onClick={playMusic} className={playing ? "" : "playBtn"}></div>
      </div>
      <p>{text}</p>
    </div>
  );
};

export default Text;
